import { TranslateService } from '@ngx-translate/core';
import { Toast } from '@capacitor/toast';
import { AlertService } from './../../services/alert/alert.service';
import { AlertController } from '@ionic/angular';
import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BarcodeScanner } from '@capacitor-community/barcode-scanner';
// import { Plugins } from '@capacitor/core';
// const { BarcodeScanner } = Plugins;

@Component({
  selector: 'app-qr-scanner',
  templateUrl: './qr-scanner.component.html',
  styleUrls: ['./qr-scanner.component.scss'],
})
export class QrScannerComponent implements OnInit {

  @Input() scanActive: boolean;
  @Output() scanningEnded = new EventEmitter<any>();
  constructor(
    private alertService: AlertService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {
    this.openQRScanner();
  }

  async checkPermission() {
    return new Promise(async (resolve, reject) => {
      const status = await BarcodeScanner.checkPermission({ force: true });
      console.log('Status permission', status)
      if (status.granted) resolve(true);
      else if (status.denied) {
        const buttons = [
          { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
          { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
        ];
        let result: any = await this.alertService.showGameAlert(this.translate.instant('PAGES.CHALLENGE.DETAIL.CAMERA_PERMISSION'), this.translate.instant('PAGES.CHALLENGE.DETAIL.CAMERA_PERMISSION_TEXT'), buttons);
        if (result && result.data && result.role === 'accept') {
          resolve(false);
          BarcodeScanner.openAppSettings();
        }
      }
    });
  }
  
  async openQRScanner() {
    BarcodeScanner.prepare();
    const allowed = await this.checkPermission();
    if (allowed) {
      BarcodeScanner.hideBackground();
      const result = await BarcodeScanner.startScan();
      if (result && result.hasContent && result.format === 'QR_CODE') {
        this.stopScan(result.content);
      } else this.stopScan();
    }
  }

  async stopScan(data?) {
    console.log('Stop scan', data);
    if (data) this.scanningEnded.emit(data);
    else this.scanningEnded.emit(undefined);
    BarcodeScanner.showBackground();
    BarcodeScanner.stopScan();
  }

}
