
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-qr-scanner-square',
  templateUrl: './qr-scanner-square.component.html',
  styleUrls: ['./qr-scanner-square.component.scss'],
})
export class QrScannerSquareComponent implements OnInit {

  @Input() scanActive: boolean;
  @Input() text: string;
  @Input() scanned: boolean;
  @Output() scanningStarted = new EventEmitter<any>();
  constructor(  ) { }

  ngOnInit() { }

  startScan() {
    this.scanningStarted.emit(true);
  }
  
}
