import { TranslateService } from '@ngx-translate/core';
import { AlertService } from 'src/app/shared/services/alert/alert.service';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { RouterConstants } from '../../config/constants/router.constants';
import { UserSession } from '../../models/user.model';
import { GamePlay, Player } from '../../models/game_play.model';
import { Location } from '@angular/common';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {

  @Input() backArrow: boolean;
  @Input() backPage: string;
  @Input() logoutHidden: boolean;
  constructor(
    public router: Router,
    private gb: GlobalDataService,
    private location: Location,
    private alertService: AlertService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {}

  async logout() {
    const buttons = [
      { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
      { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
    ];
    let result: any = await this.alertService.showGameAlert(this.translate.instant('PAGES.PROFILE.LOGOUT_TITLE'), this.translate.instant('PAGES.PROFILE.LOGOUT_DESCRIPTION'), buttons);
    if (result && result.data && result.role === 'accept') {
      //TODO: Call api logout
      this.gb.userSession = new UserSession();
      this.gb.gamePlay = new GamePlay();
      this.gb.player = new Player();
      this.router.navigateByUrl(RouterConstants.LOGIN, { replaceUrl: true });
      await this.gb.removeSession();
    }
  }

  navigateBack() {
    console.log('Navigation back', this.backPage);
    if (this.backPage) this.backNavigation();
    else this.back();
  }

  back() {
    this.location.back();
  }

  backNavigation() {
    this.router.navigateByUrl(this.backPage);
  }

}
